body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: #000000;
    background: rgb(2,0,36);
    background: -webkit-linear-gradient(left, rgb(49, 49, 49) 0%, rgb(37, 20, 20) 35%, rgb(43, 50, 51) 100%);
    background: linear-gradient(90deg, rgb(49, 49, 49) 0%, rgb(37, 20, 20) 35%, rgb(43, 50, 51) 100%);
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.introHead > h1 {
    font-size: 36px;
    letter-spacing: 0.07em;
}

.App-header {
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.common-head {
    margin: 0;
    padding: 13px 0;
}
/* Animation css*/

.typewriter-effect > h1 {
    text-transform: capitalize;
    font-size: 36px;
    color: #ffffff;
    letter-spacing: 0.1em;
    font-family: monospace;
    display: inline-block;
}
div#typing {
    font-size: 36px;
    color: #ffffff;
    font-weight: 700;
    display: inline-block;
    padding-left: 20px;
}
.typewriter-effect, .moretocome {
    position: absolute;
    right: 0;
    left: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.moretocome {
    top: 85%;
    color: #ffffff;
    letter-spacing: 0.100em;
    font-size: 25px;
}

.common-head span{
    letter-spacing: 15px;
    -webkit-animation: animate 1s infinite linear;
            animation: animate 1s infinite linear;
    font-size: 30px;
}

 @-webkit-keyframes animate{
            0%{
                color: #ed2424;
                text-shadow: none;
            }
            18%{
                color: #ed2424;
                text-shadow: none;
            }
            20%{
                color :#ffffff;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            30%{
                color: #ed2424;
                text-shadow: none;
            }
            70%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            85%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            90%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                
            }
        }

 @keyframes animate{
            0%{
                color: #ed2424;
                text-shadow: none;
            }
            18%{
                color: #ed2424;
                text-shadow: none;
            }
            20%{
                color :#ffffff;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            30%{
                color: #ed2424;
                text-shadow: none;
            }
            70%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            85%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                text-shadow: 2px 2px;
            }
            90%{
                color: #ed2424;
                text-shadow: 0 0 7px #fff900, 0 0 20px #ff6c00;
                
            }
        }


        .common-head span:nth-child(1){
            -webkit-animation-delay: .2s;
                    animation-delay: .2s;
        }
         .common-head span:nth-child(2){
            -webkit-animation-delay: .4s;
                    animation-delay: .4s;
        }
         .common-head span:nth-child(3){
            -webkit-animation-delay: .6s;
                    animation-delay: .6s;
        }
         .common-head span :nth-child(4){
            -webkit-animation-delay: .6s;
                    animation-delay: .6s;
        }
         .common-head span :nth-child(5){
            -webkit-animation-delay: .8s;
                    animation-delay: .8s;
        }
         .common-head span :nth-child(6){
            -webkit-animation-delay: .8s;
                    animation-delay: .8s;
        }
         .common-head span :nth-child(7){
            -webkit-animation-delay: .8s;
                    animation-delay: .8s;
        }
         .common-head span :nth-child(8){
            -webkit-animation-delay: .8s;
                    animation-delay: .8s;
        }


@media screen and (max-width: 767px) {
 .typewriter-effect > h1 {
       font-size: 28px;  
  }
  div#typing {
    font-size: 26px;
  }
  .moretocome{
    font-size: 14px;
  }
}   
